<script lang="js">
/* eslint-disable */
import { mapActions } from 'vuex'

export default {
  name: 'register',
  data () {
    return {
      username: '',
      password: '',
      name: '',
      contactNumber: '',
      address: ''
    }
  },
  methods: {
    ...mapActions(['register']),
    registerUser() {
      let user = {
        username: this.username,
        password: this.password,
        name: this.name,
        contact_number: this.contactNumber,
        address: this.address,
      }
      this.register(user)
      .then(res =>{
        if(res.data.success){
          this.$router.push('/pages/login')
        }
      })
      .catch(err =>{
        console.log(err);
      });
    }
  }
}
</script>

